import React, { Component } from 'react'
//import logo from './logo.svg';
import './App.css';
import './assets/css/main.css';
import { Header, Banner, Footer, HomeBody} from './components'

class Homepage extends Component {
  render(){
    return (
		<div id="page-wrapper">

			<Header at="Homepage"/>
					
			<Banner />

			<HomeBody />

			<Footer />

		</div>
    )
  }
}

export default Homepage;