import React, { Component } from 'react';
import { OneColumn, TextThird, ImageAndTextThird, Divider, TextLinkThird } from './BodyElements';

class NonCSBody extends Component
{
  render() 
  {
    return(
        <div id="main">
	        <div className="container">
    			<div className="row main-row">
                    <OneColumn title="Why Include other interests?">
                        <p>
                            This website is a personal website meant to be shared to those interested in
                            my abilities in professional capacity, so why should I include information not 
                            related to my career?  I believe that it's important to present a clear image of
                            yourself in order for others to understand you deeper.  That is why this page
                            is included, so that anyone who wishes to read this site can get to understand
                            me as a person, and not just as a programmer, a little better.
                        </p>
                    </OneColumn>
                </div>

                <Divider />

                <div className="row main-row">
                    <TextThird title="Cooking/Baking">
                        I've been baking for a few years now and have been gradually improving at it.
                        I believe that baking requires patience and practice in order to get correctly.
                        You need to be prepared to fail when trying out something new in order to eventually
                        create something great.
                    </TextThird>
                    <ImageAndTextThird image="images/birthdayCake.JPG" title="Birthday Cake">
                        A vanilla sponge with a raspberry filling and buttercream icing.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/saltwaterTaffy.JPG" title="Saltwater Taffy">
                        A bunch of vanilla saltwater taffy wrapped in parchment paper.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/cinnamonRoll.JPG" title="Cinnamon Roll">
                        One of a batch of homemade cinnamon rolls with a cream cheese frosting.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/keyLimePie.JPG" title="Key Lime Pie">
                        A key lime pie with a graham cracker crust and homemade whipped topping.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/swissRoll.JPG" title="Swiss Roll">
                        A chocolate sponge roll with a cream filling and chocolate ganache.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/bakedAlaska.JPG" title="Baked Alaska">
                        Vanilla, strawberry, and black raspberry ice cream covered in an Italian maringue and bruleed.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/baklava.JPG" title="Baklava">
                        Filo dough with a walnut pistachio filling covered with a sugar syrup.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/saltedCaramelChocolateTart.JPG" title="Salted Caramel and Chocolate Tart">
                        A chocolate tart crust with a salted caramel and peanuts filling topped with a chocolate ganache.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/fruitTart.JPG" title="Fruit Tart">
                        Vanilla custard inside a tart crust decorated with strawberry, kiwi, raspberries, and blueberries.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/cremeBrulee.JPG" title="Creme Brulee">
                        A vanilla custard with a bruleed top.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/fudge.JPG" title="Fudge">
                        A simple chocolate fudge with peanuts.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/pecanPie.JPG" title="Pecan Pie">
                        Pecan pie filling inside a homemade crust with vanilla ice cream on top.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/peanutBrittle.JPG" title="Peanut Brittle">
                        Carefully made peanut brittle with cayenne pepper for spice.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/pretzels.JPG" title="Pretzels">
                        Homemade pretzels topped with salt, cinnamon sugar, or nothing.
                    </ImageAndTextThird>
                    <ImageAndTextThird image="images/croissant.jpg" title="Croissants">
                        Croissants made with three turns, resulting in 81 layers of butter and pastry.
                    </ImageAndTextThird>
                </div>

                <Divider />
                
                <div className="row main-row">
                    <TextThird title="Film Criticism">
                        Communication is an immensly important skill and one that can be honed with practice.  The way I have gone about honing 
                        my ability to effectively communicate my thoughts is by writing my thoughts and feelings on movies I see.
                        Whenever I watch a movie in the year it was released I briefly write my thoughts on it.  I have been doing this for 
                        a couple years now dozens of brief reviews written.
                    </TextThird>
                    <TextLinkThird title='2020 Movies' link='https://docs.google.com/document/d/1WwWDbsxdmSaYtQYygTmNgBSKFR5YyWPTtvxgZ2MIXaE/edit?usp=sharing' buttonText='2020 Reviews' >
                        2020 has been a rough year for movies with so many delays and cancellations, however there still are some interesting films coming
                        out that deserve to be talked about.
                    </TextLinkThird>
                    <TextLinkThird title='Best of 2019' link='https://docs.google.com/document/d/1yYNhzpO_soYJtvyn2K5wqLThOCVcmqOq8YTBWDyzZV4/edit?usp=sharing' buttonText='2019 Movies Ranked' >
                        2019 was a fantastic year at the movies and to commemorate it, I sorted every movie I saw that year in order of most to least favorite.
                    </TextLinkThird>
                    <TextLinkThird title='2019 Movies' link='https://docs.google.com/document/d/1XRxtaIUPTpmU-U1AnqfraWD-kP7dZ0OixsAcZ8ucqtU/edit?usp=sharing' buttonText='2019 Reviews' >
                        There were a ton of interesting movies in 2019, so many of which are worth discussing.  From collosal failures, to instant classics and groundbreaking blockbusters, there was a lot to break down.
                    </TextLinkThird>
                    <TextLinkThird title='Best of 2018' link='https://docs.google.com/document/d/1-5lnrRWrXwOPT2BhuZfq2MqFETVMX21MZATc47tsux8/edit?usp=sharing' buttonText='2018 Movies Ranked' >
                        In 2018, I did not write down detailed thoughts on the movies I saw, but I still marked everything I saw and sorted them accordingly.
                    </TextLinkThird>
                </div>
            </div>
        </div>
    );
  }
}

export default NonCSBody;