import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Homepage from './Homepage';
import ResumePage from './ResumePage';
import OtherPage  from './OtherPage';

const Main = () => {
    return (
        <Switch>
            <Route exact path="/react_mysite" component={Homepage}></Route>
            <Route path="/react_mysite/home" component={Homepage}></Route>
            <Route path="/react_mysite/resume" component={ResumePage}></Route>
            <Route path="/react_mysite/other_interests" component={OtherPage}></Route>
        </Switch>
    );
}

export default Main;