import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class HeadButton extends Component
{
	render()
	{
		if (this.props.current === this.props.text)
		{
			return(
				<a href={this.props.link} className="current-page-item">{this.props.text}</a>
			)
		}
		else
		{
			return(
				<a href={this.props.link}>{this.props.text}</a>
			)
		}
	}
}

class HeadBar extends Component
{
	render()
	{
		return(
			<header id="header">
				<h1>
					<Link to="/react_mysite/home" >
						<div id="logo">Fiore</div>
					</Link>
				</h1>
				<nav id="nav">
					<Link to="/react_mysite/home">
						<HeadButton text="Homepage" current={this.props.at} />
					</Link>
					<Link to="/react_mysite/resume">
						<HeadButton text="Resume" current={this.props.at} />
					</Link>
					<Link to="/react_mysite/other_interests">
						<HeadButton text="Personal Interests" current={this.props.at} />
					</Link>
				</nav>
			</header>
		);
	}
}

class Header extends Component 
{
  render() {
    return (
    	<div id="header-wrapper">
			<div className="container">
				<div className="row">
					<div className="col-12">
						<HeadBar at={this.props.at}/>
					</div>
				</div>
			</div>
		</div>
    );
  }
}

export default Header;
