import React, { Component } from 'react';
import { PDFDisplay, OneColumn } from './BodyElements';

class ResumeBody extends Component
{
  render() 
  {
    return(
      <div id="main">
		    <div className="container">
					<div className="row main-row">
            <OneColumn title="My Resume">
              <p>
                Below you will find my resume, up to date as of Summer 2020.  If you have any questions or inquires please feel free to reach out 
                at the contact information listed.
              </p>
              <p>
                In addition to what is listed, I have begun work towards my Master's Degree utilizing NJIT's BS/MS program although I am still 
                only an undergraduate student.  While the expected graduation for my B.S. is correct, my expected graduation for my M.S. is May 2022.
              </p>
            </OneColumn>
            <PDFDisplay pdfsource='pdfs/JosephFiore_ResumeFall2020.pdf'/>
          </div>
        </div>
      </div>
    );
  }
}

export default ResumeBody;